<template>
  <div class="app-container member-list">

    <el-card class="box-card">

      <el-form :inline="true" :model="listQuery" class="demo-form-inline">
        <el-form-item label="门店">
          <el-select v-model="listQuery.store_id" placeholder="选择门店">
            <el-option label="龙珠店" value="2"></el-option>
            <el-option label="海德店" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="手机号码">
          <el-input v-model="listQuery.mobile" placeholder="请输入会员手机号码"></el-input>
        </el-form-item>
        <el-form-item label="会员卡号">
          <el-tooltip class="item" effect="dark" :content="(isCanReadCard ? '点击按钮读卡' : '放置卡片读卡')" placement="top">
            <el-input v-model="listQuery.card_id" placeholder="读卡器读取">
              <el-button v-if="isCanReadCard" slot="prepend" @click="readCard">
                <span class="read-card">
                  <icon-svg icon-class="readcard" />
                </span>
              </el-button>
              <el-button v-if="isCanReadCard" slot="append" icon="delete" @click="clearCardID">
              </el-button>
            </el-input>
          </el-tooltip>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
        </el-form-item>
      </el-form>

      <el-table v-loading.body="listLoading" element-loading-text="拼命加载中" :data="list" stripe highlight-current-row @row-click="tableRowClick" style="width: 100%">
        <el-table-column width="80" label="姓名" prop="name">
        </el-table-column>
        <el-table-column width="125" label="手机号码" prop="mobile">
        </el-table-column>
        <el-table-column label="门店" prop="store_id">
          <template scope="scope">
            <span>{{scope.row.store_id === 1 ? '英威尔海德店' : '英威尔龙珠店'}}</span>
          </template>
        </el-table-column>
        <el-table-column label="会员卡" prop="card_type">
          <template scope="scope">
            <el-tooltip class="item" :disabled="!scope.row.card_type_name" effect="dark" :content="'有效期' + formatDateTime(scope.row.card_start_time) + '至' + formatDateTime(scope.row.card_end_time) + ' ' + cardEndTime(scope.row.card_end_time)" placement="top">
              <span>{{ scope.row.card_type_name + (scope.row.card_type === 2 ? ' / 剩余' + scope.row.card_use_num + '次' : '') }}</span>
              <!-- 这里必须用个标签包起来才能正常显示 -->
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column width="125" label="卡片编号" prop="card_id">
        </el-table-column>
        <el-table-column width="140" label="卡片状态" prop="card_valid_state">
          <template scope="scope">
            <span>{{ cardValidState(scope.row.card_valid_state) }}<i v-if="scope.row.card_valid_state === 2" class="el-icon-warning" style="color: #F7BA2A"></i><i v-if="scope.row.card_valid_state === 3" class="el-icon-circle-cross" style="color: #FF4949"></i></span>
          </template>
        </el-table-column>
        <el-table-column width="100" label="销售人员" prop="sale_staff">
        </el-table-column>
        <el-table-column width="100" label="教练人员" prop="coach_staff">
        </el-table-column>
        <!-- <el-table-column label="性别" prop="sex">
                </el-table-column>
                <el-table-column label="个人照片地址" prop="photo">
                </el-table-column>
                <el-table-column label="办卡日期" prop="transact_card_time">
                </el-table-column>
                <el-table-column label="卡有效期" prop="valid_card_time">
                </el-table-column>
                <el-table-column label="微信号" prop="wechat_code">
                </el-table-column>
                <el-table-column label="生日" prop="birthday">
                </el-table-column>
                <el-table-column label="身份证号码" prop="id_card">
                </el-table-column>
                <el-table-column label="家庭住址" prop="home_address">
                </el-table-column>
                <el-table-column label="工作地址" prop="work_address">
                </el-table-column>
                <el-table-column label="来源" prop="referer">
                </el-table-column>
                <el-table-column label="备注" prop="remark">
                </el-table-column>
                <el-table-column label="新增时间" prop="add_time">
                </el-table-column> -->
        <el-table-column width="200" label="操作" prop="">
          <template scope="scope">
            <el-button size="small" @click="handleDetail(scope.$index, scope.row)">详情
            </el-button>
            <el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑
            </el-button>
            <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <div v-show="!listLoading" class="pagination-container">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="page" :page-sizes="[5, 10]" :page-size="size" layout="total, sizes, prev, pager, next" :total="total">
        </el-pagination>
      </div>
    </el-card>

  </div>
</template>

<script>
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import { lists, del } from '@/api/member';
import router from '../../router';
import { formatDateTime } from '@/utils';

export default {
  data() {
    return {
      // 总条数
      total: null,
      // 页码
      page: 1,
      size: 10,
      // 表格数据参数
      listQuery: {
        offset: '0,10',
        // limit: 20,
        store_id: undefined,
        mobile: undefined,
        card_id: undefined
        // sort: '+id'
      },
      list: null,
      listLoading: true
    }
  },
  created() {
    this.getList();
    console.log(this.isCanReadCard);
  },
  computed: {
    ...mapState([
      'winsize'
    ]),
    ...mapGetters([
      'isCanReadCard',
      'cardID'
    ])
  },
  methods: {
    ...mapMutations({
      setMemberInfo: 'member/setInfo'
    }),
    getList() {
      this.listLoading = true;
      lists(this.listQuery).then(response => {
        this.list = response.data.list;
        this.total = response.data.total;
        console.log(this.listQuery, this.list);
        this.listLoading = false;
      })
    },
    handleEdit(index, row) {
      console.log(index, row);
      // this.$message('该功能正在开发！')
      // 设置当前的会员的资料
      this.setMemberInfo(row);
      router.push({ name: '修改会员资料', params: { id: row.id } });
    },
    handleDelete(index, row) {
      console.log(index, row);
      // this.$message('该功能正在开发！')
      this.$confirm('此操作将永久删除该会员, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        del({ id: row.id }).then(response => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.getList();
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    handleDetail(index, row) {
      console.log(index, row);
      // 设置当前的会员的资料
      this.setMemberInfo(row);
      router.push({ name: '会员资料', params: { id: row.id } });
    },
    search() {
      this.getList();
    },
    tableRowClick(row) {
      console.log(row);
    },
    handleSizeChange(val) {
      this.size = val;
      this.listQuery.offset = (this.page - 1) * this.size + ',' + this.size;
      this.getList()
    },
    handleCurrentChange(val) {
      this.page = val;
      this.listQuery.offset = (this.page - 1) * this.size + ',' + this.size;
      this.getList()
    },
    readCard() {
      if (this.isCanReadCard) {
        this.actionReadCard();
        this.listQuery.card_id = this.cardID;
        console.log('读卡', this.listQuery.card_id, this.cardID);
      }
    },
    ...mapActions([
      'actionReadCard'
    ]),
    clearCardID() {
      this.listQuery.card_id = undefined;
    },
    formatDateTime(time) {
      return formatDateTime(time)
    },
    cardEndTime(time) {
      const now = Math.round(new Date().getTime() / 1000);
      const days = Math.round((time - now) / (60 * 60 * 24))
      return days >= 0 ? '还有' + days + '天到期' : '已到期';
    },
    // 卡片状态
    cardValidState(card_valid_state) {
      let state;
      switch (card_valid_state) {
        case 0:
          state = '无有效期';
          break;
        case 1:
          state = '正常有效';
          break;   
        case 2:
          state = '剩余不足一月';
          break;
        case 3:
          state = '已过期';
          break;
        default:
          state = '无有效期';      
      }
      return state;
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss">
.demo-table-expand {
  font-size: 0;
}

.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}

.el-table__expanded-cell {
  padding: 10px;
}

.el-card {
  margin: 20px 0;
  &:last-of-type {
    margin-bottom: 0;
  }
  &:first-of-type {
    margin-top: 0;
  }
}

.read-card .svg-icon {
  font-size: 20px;
}

.member-list .pagination-container {
  margin-top: 10px;
}
</style>



